
import { defineComponent } from 'vue'
import { selectedDeckGlobal, decks, deck, uniqueAllCards, findCard } from '@/global'
import * as type from '@/types'
import { getDecks, getDeck, setDecks, setDeck } from '@/API'
export default defineComponent({
    setup() {
        return { selectedDeckGlobal, decks, deck, uniqueAllCards, findCard }
    },
    mounted() {
        window.addEventListener('click', event => {
            if (event.target == document.getElementById('comboAddEditModal')) {
                this.closeComboAddEditModal()
            }
            if (event.target == document.getElementById('comboDeleteModal')) {
                this.closeComboDeleteModal()
            }
        })
    },
    data() {
        return {
            editAdd: 'add',
            editComboIndex: 0,
            deleteComboIndex: 0,
            comboCards: ['1. Card', '2. Card', '3. Card', '4. Card', '5. Card'] as any[],
            comboCardsType: [] as string[],
        }
    },
    methods: {
        openComboAddModal() {
            this.editAdd = 'add'
            var modal = document.getElementById('comboAddEditModal')
            if (modal) modal.style.display = 'block'
        },
        closeComboAddEditModal() {
            this.inputReset()
            var modal = document.getElementById('comboAddEditModal')
            if (modal) modal.style.display = 'none'
        },
        openComboEditModal(index: number) {
            this.editAdd = 'edit'
            this.editComboIndex = index
            for (let card of this.deck.combos[index].cards) {
                this.comboCards[this.deck.combos[index].cards.findIndex(c => c == card)] = card
            }
            this.changeType()
            var modal = document.getElementById('comboAddEditModal')
            if (modal) modal.style.display = 'block'
        },
        openComboDeleteModal(index: number) {
            this.deleteComboIndex = index
            var modal = document.getElementById('comboDeleteModal')
            if (modal) modal.style.display = 'block'
        },
        closeComboDeleteModal() {
            this.inputReset()
            var modal = document.getElementById('comboDeleteModal')
            if (modal) modal.style.display = 'none'
        },
        addCombo() {
            let cardArray = [] as string[]
            for (let i = 0; i < 5; i++) {
                if (typeof this.comboCards[i] === 'object') {
                    cardArray.push(this.comboCards[i])
                } else {
                    if (!this.comboCards[i].includes('. Card')) {
                        let card = this.deck.cards.find(c => c.id == this.comboCards[i])
                        if (card) {
                            cardArray.push(card.id)
                        }
                    }
                }
            }
            if (cardArray.length) {
                switch (this.editAdd) {
                    case 'add':
                        this.deck.combos.push({ cards: cardArray, active: true } as type.Combo)
                        break
                    case 'edit':
                        this.deck.combos[this.editComboIndex] = {
                            cards: cardArray,
                            active: this.deck.combos[this.editComboIndex].active,
                        } as type.Combo
                        break
                }
            }
            this.checkComboCardGroups()
            this.safeDeck()
            this.closeComboAddEditModal()
        },
        deleteCombo() {
            this.deck.combos.splice(this.deleteComboIndex, 1)
            this.safeDeck()
            this.closeComboDeleteModal()
        },
        inputReset() {
            this.comboCards = ['1. Card', '2. Card', '3. Card', '4. Card', '5. Card']
            this.changeType()
        },
        safeDeck() {
            this.decks[this.decks.findIndex(d => d.name == this.deck.name)] = this.deck
            setDeck(this.deck)
            setDecks(this.decks)
        },
        changeType() {
            for (let i = 0; i < 5; i++) {
                let card = this.deck.cards.find(c => c.id == this.comboCards[i])
                card ? (this.comboCardsType[i] = card.type) : (this.comboCardsType[i] = '')
            }
        },
        checkComboCardGroups() {
            for (let cardGroup of this.deck.cardGroups) {
                cardGroup.active = cardGroup.cards.some(cardId => this.deck.cards.map(c => c.id).includes(cardId))
            }
            for (let combo of this.deck.combos) {
                combo.active = combo.cards.every(card =>
                    typeof card === 'object' ? this.deck.cardGroups.find(c => c.id == card.id)?.active : this.deck.cards.map(c => c.id).includes(card)
                )
            }
        },
    },
})
